import React from "react";
import styled from "styled-components";

import { Introduction } from "./1.1 introduction/Introduction";
import { DinoGlide } from "./2 experiences/DinoGlide";
import { UNC } from "./3 education/UNC";

const DesktopPageStyler = styled.div`
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
  transform: scale(0.8);
  transform-origin: top;
  margin-top: 2%;
`;

export const DesktopPage = () => {
  return (
    <DesktopPageStyler>
      <Introduction />
      <DinoGlide />
      <UNC />
    </DesktopPageStyler>
  );
};
